html {
	background-color: black;
}

.formStyling {
	width: 25%;
	height: 35px;
	background-color: black;
	color: white;
	border: 1px solid white;
	border-radius: 5px;
	margin-top: 10px;
	padding: 5px 5px;
}

.royce {
	width: 700px;
	position: absolute;
	left: 40%;
	margin-left: -220px;
	margin-top: -350px;
	z-index: -1;
}

.header {
	color: white;
	font-size: 22px;
}

::placeholder {
	/* Chrome, Firefox, Opera, Safari 10.1+ */
	color: white;
	opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
	/* Internet Explorer 10-11 */
	color: white;
}

::-ms-input-placeholder {
	/* Microsoft Edge */
	color: white;
}

.parent {
	margin-left: 80px;
	margin-top: 150px;
}

.submitForm {
	width: 26%;
	margin-top: 10px;
	height: 38px;
	border-radius: 5px;
	border: 1px solid white;
	cursor: pointer;
}

.logo {
	position: absolute;
	width: 470px;
	z-index: 2;
	margin-left: -120px;
	margin-top: -90px;
}

.right {
	margin-left: 70%;
	top: 0%;
	margin-top: -380px;
}

.description {
	color: white;
	position: absolute;
	margin-top: 150px;
	text-align: center;
	font-weight: 800;
	font-size: 22px;
	left: 60%;
	margin-right: 20px;
}

.googleform {
	margin-top: 100px;
	width: 25%;
	height: 38px;
	border-radius: 5px;
	cursor: pointer;
	color: white;
	background-color: black;
	border: 1px solid white;
}

@media only screen and (max-width: 600px) {
	.header {
		font-size: 14px;
		/* margin-left: 15px; */
	}
	.formStyling {
		width: 75%;
		height: 35px;
		background-color: black;
		color: white;
		border: 1px solid white;
		border-radius: 5px;
		margin-top: 10px;
	}

	.submitForm {
		width: 80%;
		margin-left: 1px;
	}

	.googleform {
		width: 80%;
		margin-left: 1px;
	}

	.royce {
		width: 300px;
		position: relative;
		z-index: -1;
		left: 50%;
	}

	.logo {
		width: 300px;
		margin-top: -170px;
		margin-left: -55px;
	}

	.right {
		margin-left: 22%;
		top: 0%;
		margin-top: -320px;
	}

	.description {
		color: white;
		margin-top: 350px;
		text-align: center;
		font-weight: 800;
		font-size: 22px;
		max-width: 70%;
		margin-left: 10%;
		left: 0%;
	}

	.logo {
		padding-left: 10px;
		padding-right: 10px;
	}
}

@media only screen and (min-width: 1800px) {
	.royce {
		width: 1000px;
		margin-left: -300px;
	}
}
